@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;700&display=swap);
.movies {
  display: flex;
  flex-wrap: wrap;
}

.movie {
  position: relative;
  background-color: #282c34;
  border: 2px solid rgb(101, 102, 102);
  color: #61daee;
  box-shadow: 5px 5px 3px #282c34;
}

.movie img {
  position: absolute;
}

.movie__date {
  margin: 0;
}

.movie__title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.movie__year {
  opacity: 0.8;
  font-weight: 300;
}
.movie__genres {
  display: flex;
  list-style-type: none;
  opacity: 0.8;
  font-weight: 300;
  flex-wrap: wrap;
}

.movie__summary {
  color: rgb(210, 217, 224);
}

@media screen and (max-width: 479px) {
  .movies {
    margin: 5vh 2vw 0 2vw;
  }

  .movie {
    width: 75vw;
    margin: 0 auto 0 auto;
    margin-top: 5vh;
  }

  .movie img {
    width: 15vw;
    height: 12vh;
    top: 3vh;
    left: 10px;
  }

  .movie__date {
    margin: 3vh 5vw 3vh 20vw;
  }

  .movie__title {
    font-size: 1.2rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
  }

  .movie__year {
    font-size: 6px;
    margin-bottom: 0.3rem;
    letter-spacing: 0.03rem;
  }

  .movie__genres {
    margin-bottom: 0.5rem;
  }

  .genres__genre {
    font-size: 6px;
    margin-right: 0.2rem;
    letter-spacing: 0.03rem;
  }

  .movie__summary {
    font-size: 0.8rem;
    letter-spacing: 0.03rem;
    line-height: 1rem;
  }
}

@media screen and (min-width: 480px) {
  .movies {
    margin: 45px 2vw 0 2vw;
    padding: 0 25px;
  }

  .movie {
    display: flex;
    width: 35vw;
    margin: 0 auto 0 auto;
    margin-top: 10vh;
  }

  .movie img {
    width: 120px;
    height: 190px;
    top: -25px;
    left: -20px;
    position: relative;
    box-shadow: 3px 3px 10px black;
  }

  .movie__date {
    margin: 20px 5px 20px 0px;
  }

  .movie__title {
    font-size: 1.4rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
  }

  .movie__year {
    font-size: 10px;
    margin-bottom: 0.3rem;
    letter-spacing: 0.03rem;
  }

  .movie__genres {
    margin-bottom: 0.5rem;
  }

  .genres__genre {
    font-size: 10px;
    margin-right: 0.2rem;
    letter-spacing: 0.03rem;
  }

  .movie__summary {
    font-size: 12px;
    letter-spacing: 0.05rem;
    line-height: 1.4rem;
  }
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input:focus {
  outline: none;
}
a {
  color: inherit;
  text-decoration: none;
}

html {
  width: 100%;
  height: 100%;
  font-family: "Noto Sans KR", sans-serif;
  font-size: 14px;
}

body {
  margin: 0;
  z-index: -1;
  height: 100%;
  background-color: rgb(235, 237, 237);
}

.header {
  width: 100%;
  background-color: #20232a;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #61daee;
  font-weight: 300;
  position: fixed;
  top: 0;
  left: auto;
  z-index: 2;
}

.container {
  display: flex;
  width: 100%;
  height: 100%;
}

.loader {
  display: flex;
  width: 100%;
  height: 100vh;
  color: #282c34;
  justify-content: center;
  align-items: center;
}

.loader__text {
  font-size: 50px;
}

@media screen and (max-width: 479px) {
  .header {
    font-size: 1.2rem;
    height: 5vh;
    letter-spacing: 0.12rem;
  }
}

@media screen and (min-width: 480px) {
  .header {
    font-size: 1.4rem;
    height: 45px;
    letter-spacing: 0.12rem;
  }
}

